"use strict";
const adminAccount_1 = require('./store/adminAccount');
const tweet_1 = require('./store/tweet');
const socket_1 = require('./service/socket');
const localhost_1 = require('./database/localhost');
const tweetDB = new localhost_1.ActionDatabase('tweet');
const oldActins = tweetDB.load();
const tweetStore = tweet_1.default({ actions: oldActins });
tweetStore.addChangeListener(() => console.log(tweetStore.get().toJS()));
tweetStore.addChangeListener(() => {
    tweetDB.save(tweetStore.lastAction);
    tweetDB.commit();
});
adminAccount_1.default.addChangeListener(() => console.log(adminAccount_1.default.get().toJS()));
socket_1.socketConnect();
