"use strict";
const immutable_1 = require('immutable');
const initialModel = {
    created_at: "",
    id: 0,
    id_str: '',
    text: '',
    in_reply_to_status_id: null,
    in_reply_to_status_id_str: null,
    in_reply_to_user_id: null,
    in_reply_to_user_id_str: null,
    in_reply_to_screen_name: null,
    user: {
        id: 0,
        id_str: '',
        screen_name: '',
    },
    retweeted_status: {
        id: 0,
        id_str: '',
    }
};
class TweetModel extends immutable_1.Record(initialModel) {
    retweeted() {
        return this.retweeted_status.id === 0 ? false : true;
    }
    post() {
        return {
            status: this.text,
            in_reply_to_status_id: this.in_reply_to_status_id
        };
    }
    replay(tweet) {
        const replayStatus = `@${this.user.screen_name} ${tweet.post().status}`;
        return new TweetModel({
            text: replayStatus,
            in_reply_to_status_id: this.id_str
        });
    }
    //in_reply_to_status_idstringtweetId
    destroy() {
        return {
            id_str: this.id_str
        };
    }
}
exports.TweetModel = TweetModel;



