"use strict";
const adminAccount_1 = require('../constant/adminAccount');
const flux_1 = require('../flux');
function addAccount(id, account) {
    flux_1.command(adminAccount_1.ADDACCOUNT, { id: id, account: account });
}
exports.addAccount = addAccount;
function updateAccount(id, params) {
    flux_1.command(adminAccount_1.UPDATE, { params: params });
}
exports.updateAccount = updateAccount;



