"use strict";
const tweet_1 = require('../constant/tweet');
const flux_1 = require('../flux');
const request = require('axios');
function addTweet(id, tweet) {
    flux_1.command(tweet_1.ADDTWEET, { id: id, tweet: tweet });
}
exports.addTweet = addTweet;
function postTweet(userId, tweet) {
    return request.post('/tweet/update', Object.assign({}, { options: tweet.post() }, { key: userId }))
        .then(value => {
        return value.data;
    });
}
exports.postTweet = postTweet;
function destroyTweet(userId, tweet) {
    return request.post('/tweet/destroy', Object.assign({}, { options: tweet.destroy() }, { key: userId }))
        .then(res => res.data);
}
exports.destroyTweet = destroyTweet;



